.cripto-first {
  background: #02735e;
  width: 95%;
  max-width: 500px;
  border-radius: 20px;
  color: white;
  padding: 20px;
  text-align: center;
}

.graphic .graph {
  width: 100%;
  height: 100%;
  display: grid;
}

.capitalization h2 {
  margin-bottom: 20px;
}

.capitalization-table {
  width: 90%;
  margin: auto;
  text-align: center;
  background: white;
  border-radius: 5px;
  padding: 10px;
  color: black;
  margin-bottom: 20px;
  font-size: 12px;
}
